export class OnsiteItem {
    constructor(
        public id: number,
        public img: string,
        public title: string,
        private _status: number,
        public summary: string,
        public date: Date
    ) {}

    get status () {
        return OnsiteStatus.status[this._status].content;
    }

    get color () {
        return OnsiteStatus.status[this._status].color;
    }

    get datestr () {
        const now = new Date();
        if (now.getFullYear() === this.date.getFullYear()) {
            return (this.date.getMonth() + 1) + '' + this.date.getDate() + '';
        }else {
            return this.date.getFullYear() + '-' + (this.date.getMonth() + 1) + '-' + this.date.getDate();
        }
    }
}
export class OnsiteDetail {
    constructor() {}
}
export class OnsiteItemStatus {
    constructor(
        public id: number,
        public content: string,
        public color: string = '#0099CC') {}
}

/**
 * 
 */
export class OnsiteStatus {
    public static readonly all = 0;
    /**
     * 
     */
    public static readonly success = 1;
    /**
     * 
     */
    public static readonly draft = 2;
    /**
     * 
     */
    public static readonly onsite = 3;
    /**
     * 
     */
    public static readonly internal = 4;
    /**
     * 
     */
    public static readonly reject = 5;
    /**
     * 
     */
    public static readonly invalid = 6;
    /**
     * status, 
     */
    public static readonly status: OnsiteItemStatus[] = [
        new OnsiteItemStatus(0, '', '#259b24'),
        new OnsiteItemStatus(1, '', '#259b24'),
        new OnsiteItemStatus(2, '', '#e51c23'),
        new OnsiteItemStatus(3, '', '#ff9800'),
        new OnsiteItemStatus(4, '', '#ff9800'),
        new OnsiteItemStatus(5, '', '#e51c23'),
        new OnsiteItemStatus(6, '', '#e51c23')
      ];
}



