import { Injectable } from '@angular/core';
import { OnsiteItem, OnsiteStatus } from '../../class/onsite-item';
import { PickerData, PickerOptions, PickerService } from 'ngx-weui/picker';
import { FormBase,
  InputForm,
  TextareaForm,
  RelationForm,
  LinkForm,
  PhotosForm,
  LabelForm,
  AvatarsForm,
  ScheduleForm,
  Avatar,
  Relation,
  Schedule
} from '../../class/form-base';

@Injectable()
export class WorkService {
  private onsiteList: OnsiteItem[];
  contractTitles: Relation[];
  inventoryFormsValue: any[] = [];

  constructor(private srv: PickerService) {
    this.contractTitles = [
      new Relation(1, '', '--12017'),
      new Relation(2, '', '--12017'),
      new Relation(3, '', '--12017'),
      new Relation(4, '', '--12017'),
      new Relation(5, '', '--12017'),
      new Relation(6, '', '--12017'),
      new Relation(7, '', '--12017'),
    ];
    this.onsiteList = [
      new OnsiteItem(0, 'https://modao.cc/uploads3/images/1275/12759365/raw_1505547729.jpeg',
        '35#1-50#', OnsiteStatus.onsite, '',
        new Date(2017, 8 , 16)),
      new OnsiteItem(1, 'https://modao.cc/uploads3/images/1275/12759572/raw_1505548205.jpeg',
        '', OnsiteStatus.draft, '',
        new Date(2017, 8 , 2)),
      new OnsiteItem(2, 'https://modao.cc/uploads3/images/1275/12759590/raw_1505548242.jpeg',
        'YJ140T-6', OnsiteStatus.internal, '',
        new Date(2017, 7 , 20)),
      new OnsiteItem(3, 'https://modao.cc/uploads3/images/1275/12759608/raw_1505548276.jpeg',
        '', OnsiteStatus.success, '',
        new Date(2017, 6 , 11)),
      new OnsiteItem(4, 'https://modao.cc/uploads3/images/1275/12759630/raw_1505548320.jpeg',
        '1#B', OnsiteStatus.success, '',
        new Date(2016, 11 , 30))
    ];
    this.onsiteList = this.onsiteList.concat(this.onsiteList);
  }

  getOnsite(id: number, callback: Function) {
    callback(this.onsiteList[id]);
  }

  getOnsiteList(callback: Function) {
    callback(this.onsiteList);
  }

  getInventoryForms() {
    const inventoryForms: FormBase<any>[] = [
      new InputForm({key: 'division', label: '', required: false, placeholder: '100', order: 1}),
      new TextareaForm({key: 'suboption', label: '', required: true, placeholder: '300', order: 2}),
      new TextareaForm({key: 'feature', label: '', required: false, placeholder: '500', order: 3}),
      new InputForm({key: 'propose', label: '', required: true, placeholder: '', order: 4}),
      new InputForm({key: 'engineeringquantity', label: '', required: true, placeholder: '', order: 5}),
      new InputForm({key: 'unitprice', label: '()', required: true, placeholder: '', order: 6}),
      new InputForm({key: 'totalprice', label: '()', required: true, placeholder: '', order: 7}),
      new TextareaForm({key: 'remark', label: '', required: false, placeholder: '', order: 8})
    ];
    return inventoryForms;
  }

  getWorkDetailForms(title: string, ) {
    const content = '--12017-QZ-001';
    const detail = ' : ||\
                        11#B ||\
                     ||\
                        1d300104m2d500104m1#B4d20084m||\
                     ||\
                        1||\
                        2||\
                        31#B4D200||\
                     ';

    const schedules: Schedule[] = [
      new Schedule('circlecheck', '#259b24', '#259b24', ' ', '', new Date(2017, 7, 2, 21, 47)),
      new Schedule('circlecheck', '#259b24', '#e51c23', '   ', '-',
        new Date(2017, 7, 3, 21, 47), '2'),
      new Schedule('circlecheck', '#259b24', '#259b24', '   ', '',
        new Date(2017, 7, 3, 8, 47), ''),
      new Schedule('circlecheck', '#259b24', '#259b24', ' ', '', new Date(2017, 7, 3, 9, 47), ''),
      new Schedule('circlecheck', '#259b24', '#259b24', ' ', '', new Date(2017, 7, 4, 10, 47), ''),
      new Schedule('circlecheck', '#259b24', '#259b24', ' ', '', new Date(2017, 7, 4, 17, 47), ''),
      new Schedule('circlecheck', '#259b24', '#259b24', ' ', '', new Date(2017, 7, 4, 18, 47), ''),
      new Schedule('circlecheck', '#259b24', '#259b24', ' ', '', new Date(2017, 7, 10, 19, 47), '')
    ];


    const workDetailForms: FormBase<any>[] = [
      new RelationForm({key: 'contract', label: '', isForm: false,
        title: '',
        content: '--12017', order: 1}),
      new RelationForm({top: 'top', key: 'finished', label: '', isForm: false,
        title: '',
        content: '--12017', order: 2}),
      new LinkForm({top: 'top', key: 'onsite', isForm: false,
        label: title, icon: 'onsite', color: '#d4237a', fontColor: '#101010', order: 3}),
      new LabelForm({key: 'number', label: '', isForm: false, value: content, order: 4}),
      new LabelForm({key: 'content', label: '', isForm: false, approved: true, value: detail, order: 5}),
      new LabelForm({key: 'type', label: '', isForm: false, value: '', order: 6}),
      new LabelForm({key: 'reason', label: '', isForm: false, value: '', order: 7}),
      new LabelForm({key: 'propose', label: '', isForm: false, value: '', order: 8}),
      new LabelForm({key: 'profession', label: '', isForm: false, value: '', order: 9}),
      new LabelForm({key: 'money', label: '', isForm: false, value: '44,494.31 ', order: 10}),
      new PhotosForm({top: 'top', key: 'photos', isForm: false, imgUrls: [
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1275/12759630/raw_1505548320.jpeg'], order: 11}),
      new ScheduleForm({top: 'top', key: 'schedule', isForm: false, label: '', name: '',
        icon: 'users', color: '#009688', fontColor: '#259b24', schedules: schedules, order: 12}),
      new AvatarsForm({key: 'cc', label: '', avatars: [ new Avatar('avatar-5', ''), new Avatar('avatar-8', '')],
        isForm: false, order: 13})
    ];
    return workDetailForms;
  }

  getWorkForms() {
    const contractTitles: string[] = ['', '',
      '', '', ''
    ];
    const workForms: FormBase<any>[] = [
      new RelationForm({key: 'contract', label: '', required: true, titles: contractTitles,  title: '', order: 1}),
      new InputForm({top: 'top', key: 'overview', label: '', required: true, placeholder: '20', order: 2}),
      new TextareaForm({key: 'details', label: '', required: true, placeholder: '300', order: 3}),
      new LabelForm({top: 'top', key: 'type', label: '', required: false, value: '', order: 4}),
      new InputForm({key: 'reason', label: '', required: true, placeholder: '', order: 5}),
      new InputForm({key: 'propose', label: '', required: true, placeholder: '', order: 6}),
      new InputForm({key: 'profession', label: '', required: false, placeholder: '', order: 7}),
      new InputForm({key: 'money', label: '()', required: false, placeholder: '', order: 8}),
      new PhotosForm({top: 'top', key: 'photos', required: false, imgUrls: [
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1275/12759630/raw_1505548320.jpeg'], order: 9}),
      new LinkForm({top: 'top', key: 'link', required: false, url: '', label: '', icon: 'link', order: 10}),
      new LinkForm({top: 'top', key: 'plus', required: false, url: '/work/add/inventory', value: this.inventoryFormsValue,
        label: '', icon: 'add', order: 11}),
      new LinkForm({top: 'top', key: 'users', required: false,
        label: '', icon: 'users', color: '#009688', fontColor: '#259b24', order: 12}),
      new AvatarsForm({key: 'approval', label: '', required: false,
        avatars: [ new Avatar('avatar-5', ''), new Avatar('avatar-8', '')], order: 13}),
      new AvatarsForm({key: 'cc', label: '', required: false, order: 14})
    ];
    return workForms;
  }

  onShowBySrv(workForm: FormBase<any>, callback: Function) {
    if (!workForm.isForm) {
      return;
    }
    const data: string[] = workForm['titles'] as string[];
    const index: number = Math.floor(data.length / 2) as number;
    const value: string = data[index];
    console.log(index);
    const options: PickerOptions = {cancel: '', confirm: '', backdrop: true};
    this.srv.show(data, value, [], options).subscribe((res: any) => {
      this.contractTitles.forEach(element => {
        if (element.title === res.value) {
          callback(element);
          return;
        }
      });
    });
  }

}



